import React, { useState, useEffect } from "react";
import { Button, Spin, Space } from "antd";
import { Share } from "components";
import AdSense from "react-adsense";

import Image10 from "assets/images/10.jpg";
import Image20 from "assets/images/20.jpg";
import Image30 from "assets/images/30.jpg";
import Image40 from "assets/images/40.jpg";
import Image50 from "assets/images/50.jpg";

export const Result = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [text, setText] = useState("");
  const [age, setAge] = useState("");
  const [total, setTotal] = useState(0);

  useEffect(() => {
    let score = 0,
      sum = 0;
    for (let i = 1; i <= 8; i += 1) {
      score = localStorage.getItem(`color-qna-${i}`)
        ? Number(localStorage.getItem(`color-qna-${i}`))
        : 1;
      sum += score;
    }
    setTotal(sum);
    console.log(sum);
    if (7 <= sum && sum <= 12) {
      setAge("20세 이하");
      setText(
        "여권에 뭐라고 쓰여있는지는 중요하지 않아요. 마음걱정 하나 없는 청소년 입니다. 실제로 스무살이 넘으셨다면 축하합니다. 지금까지 인생에 대한 끊임없는 열정과 사랑을 간직해 오셨습니다. 앞으로도 계속 빛을 내시길 바랍니다."
      );
    } else if (13 <= sum && sum <= 20) {
      setAge("20 ~ 29세");
      setText(
        "활동적이고 창의적이고 생동감이 넘치지만 어른이라고 할 수 있습니다. 어린 시절과 10대 시절은 지났지만 좋은 것만 남기고 불필요한 것들은 모두 버렸습니다. 그렇다고 해서 재미가 없다는 건 아닙니다. 여러분의 삶을 예전만큼 흥미진진 합니다."
      );
    } else if (21 <= sum && sum <= 28) {
      setAge("30 ~ 39세");
      setText(
        "여전히 활동적이고 아이디어들의 호기심이 많지만 책임감과 사려가 깊어요. 생각해보면 그건 아이들의 삶에 대한 기쁨과 어른들의 독립 사이에 완벽한 균형이에요. 전성기를 누리고 있습니다."
      );
    } else if (29 <= sum && sum <= 35) {
      setAge("40 ~ 49세");
      setText(
        "성숙하고 경험이 많고 삶을 어떻게 살아야 하는지 정확히 알고 있습니다. 쓸데없는 일을 할 시간이 없고 단호하고 열심히 일을 합니다. 무엇을 원하고 어떻게 그것을 얻는지 알고 있습니다. 계속해서 주변의 모든 사람들에게 필요한 영감을 주시길 바랍니다."
      );
    } else if (36 <= sum) {
      setAge("50세 이상");
      setText(
        "전혀 나쁜뜻이 아닙니다. 반대로 여러분은 현명하고 침착합니다. 인생이 뭔지 알고 편안함을 높이 평가하는 시기입니다. 모든 사람이 조언을 구하는 상대 입니다. 게다가 언제나 믿을만 합니다. 요즘에는 찾아보기 힘든 장점이 있기 때문에 친구들은 무슨 수를 써더라도 여러분을 지켜야 해요."
      );
    }

    setTimeout(() => {
      setIsLoading(false);
    }, 2000);
  }, []);
  return (
    <div className="">
      {isLoading ? (
        <div className="pt-8 flex justify-center">
          <Space size="middle">
            <Spin size="large" />
          </Space>
        </div>
      ) : (
        <React.Fragment>
          <div className="pt-8">
            <div className="text-center text-2xl font-bold">
              당신의 정신연령 나이는?
            </div>

            <AdSense.Google
              style={{ display: "block" }}
              client="ca-pub-9130836798889522"
              slot="4567187006"
              format="auto"
              responsive="true"
            />

            <div className="text-center pt-4 text-4xl">{age}</div>
            <div className="text-left pt-4 pb-4">{text}</div>
            {total >= 7 && total <= 12 ? (
              <img src={Image10} className="w-full" alt="10" />
            ) : (
              ""
            )}
            {total >= 13 && total <= 20 ? (
              <img src={Image20} className="w-full" alt="20" />
            ) : (
              ""
            )}
            {total >= 21 && total <= 28 ? (
              <img src={Image30} className="w-full" alt="30" />
            ) : (
              ""
            )}
            {total >= 29 && total <= 35 ? (
              <img src={Image40} className="w-full" alt="40" />
            ) : (
              ""
            )}
            {total >= 36 ? (
              <img src={Image50} className="w-full" alt="50" />
            ) : (
              ""
            )}
          </div>
          <Share />
          <div className="text-center">
            <div className="text-center pb-4 text-3xl">
              다른 테스트 하러가기
            </div>
            <Button style={{ width: 336 }} type="primary" size="large">
              <a href="https://mbtilovetest.com">MBTI 이상형 테스트</a>
            </Button>
          </div>
        </React.Fragment>
      )}
    </div>
  );
};
